body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar .correct {
    -webkit-animation: .4 correct;
            animation: .4 correct;
}
.navbar .incorrect {
    -webkit-animation: .4s incorrect;
            animation: .4s incorrect;
}
  
@-webkit-keyframes correct {
    0% {
        color: white;
    }
  
    50% {
        color: #2e7d32;
    }
  
    100% {
        color: white;
    }
}
  
@keyframes correct {
    0% {
        color: white;
    }
  
    50% {
        color: #2e7d32;
    }
  
    100% {
        color: white;
    }
}
  
@-webkit-keyframes incorrect {
    0% {
        color: white;
    }
  
    50% {
        color: #c62828;
    }
  
    100% {
        color: white;
    }
}
  
@keyframes incorrect {
    0% {
        color: white;
    }
  
    50% {
        color: #c62828;
    }
  
    100% {
        color: white;
    }
}
.navbar {
  background: #1c2b83;
  min-height: 70px;
  position: fixed;
  width: 100%;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  color: white;
  font-size: calc(10px + (24 - 10) * (100vw - 320px) / (1200 - 400));
  z-index: 99;
  white-space: nowrap;
}

.navbar ul {
  margin: 0;
  padding: 0;
}

.navbar li {
  list-style-type: none;
}

.itemLeft {
  display: inline-block;
  padding: 26px 0px;
  width: 25%;
  text-align: center;
}

.itemCenter {
  display: inline-block;
  padding: 26px 0px;
  width: 50%;
  text-align: center;
}

.itemRight {
  display: inline-block;
  padding: 26px 0px;
  width: 25%;
  text-align: center;
}

.navbar a {
  text-decoration: none;
  color: white;
  font-weight: 900;
}

@media (max-width: 420px) {
  .navbar li:first-child {
    width: 25%;
  }

  .navbar li:nth-child(2) {
    width: 35%;
  }

  .navbar li:last-child {
    width: 40%;
  }
}
.click-item {
  background-size: cover;
  background-position: center;
  height: 170px;
  width: 170px;
  margin: 8px 25px;
  border: 5px solid rgb(241, 241, 241);
  border-radius: 3px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  cursor: pointer;
}

.click-item:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  transition: 0.1s;
}

.shake {
  -webkit-animation: shake .5s 0.0125s;
          animation: shake .5s 0.0125s;
}

@-webkit-keyframes shake {
  from,
  to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  12.5%,
  37.5%,
  62.5%,
  87.5% {
    -webkit-transform: translate(-10px);
            transform: translate(-10px);
  }

  25%,
  50%,
  75% {
    -webkit-transform: translate(10px);
            transform: translate(10px);
  }
}

@keyframes shake {
  from,
  to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  12.5%,
  37.5%,
  62.5%,
  87.5% {
    -webkit-transform: translate(-10px);
            transform: translate(-10px);
  }

  25%,
  50%,
  75% {
    -webkit-transform: translate(10px);
            transform: translate(10px);
  }
}

@media (max-width: 750px) {
  .click-item {
    margin: 10px;
  }
}

@media (max-width: 500px) {
  .click-item {
    margin: 10px;
  }
}

.wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  min-height: 90%;
  width: 90%;
  padding-top: 110px;
  padding-bottom: 40px;
  margin: 0 auto;
}

